import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'dateInfo'
})
export class DateInfoPipe implements PipeTransform {

  transform(value: string, type: string): unknown {

    let dateFormatted = value.split( '/' ).join( '-' );
    let date = new Date( dateFormatted );
    
    if ( type === 'H' ) {
      return date.toLocaleString('es-PA', { hour: 'numeric', minute: 'numeric', hour12: true });
    }

    return null;
  }

  dateToWord = ( month: number ) => {

    switch(month) {

      case 1:
        return 'Enero';
      case 2:
        return 'Febrero';
      case 3:
        return 'Marzo';
      case 4:
        return 'Abril';
      case 5:
        return 'Mayo';
      case 6:
        return 'Junio';
      case 7:
        return 'Julio';
      case 8:
        return 'Agosto';
      case 9:
        return 'Septiembre';
      case 10:
        return 'Octubre';
      case 11:
        return 'Noviembre';
      case 12:
        return 'Diciembre';
    }
    return;
  }
}
