import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { PostService } from 'src/app/services/post.service';
import { LoadingService } from 'src/app/services/loading.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  constructor( private service: PostService, private loadingService: LoadingService ) { }

  ngOnInit(): void {
  }

  search( searchText: string ) {

    if (searchText.length > 2) {
      
      this.loadingService.on(true, false);
      this.service.getSearchPost( searchText ).subscribe( (data: any) => {

        this.service.postSearched = data.payload.posts;
        this.loadingService.off(false, true);
      }, (error) => {
      });
    } else {
      
      this.loadingService.off(false, false);
    }


    
  }
}
