import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'calendarDate'
})
export class CalendarDatePipe implements PipeTransform {

  transform(value: String, pattern: String): String {

    let dateFormatted = value.split('/').join('-');
    let date = new Date(dateFormatted);

    if (pattern === 'M') {
      return (date.getMonth() + 1).toString();
    } else if (pattern === 'Y') {
      return date.getFullYear().toString();
    } else {
      return date.getDate().toString();
    }
  }

  dateToWord = ( month: number ) => {

    switch(month) {

      case 1:
        return 'ENE';
      case 2:
        return 'FEB';
      case 3:
        return 'MAR';
      case 4:
        return 'ABR';
      case 5:
        return 'MAY';
      case 6:
        return 'JUN';
      case 7:
        return 'JUL';
      case 8:
        return 'AGO';
      case 9:
        return 'SEP';
      case 10:
        return 'OCT';
      case 11:
        return 'NOV';
      case 12:
        return 'DEC';
    }
    return;
  }
}