import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PostService } from 'src/app/services/post.service';

@Component({
  selector: 'app-post-info',
  templateUrl: './post-info.component.html',
  styleUrls: ['./post-info.component.css']
})
export class PostInfoComponent {

  payload: any = {};
  post: any = {};

  loading: boolean;

  linkWsp: string;

  linkIg: string;

  tags: any[] = [];

  constructor( private activatedRoute: ActivatedRoute, private service: PostService ){

    this.loading = true;

    this.activatedRoute.params.subscribe( params =>{
        this.post = params;

        service.getPostInfo( params['id'] ).subscribe( (data: any) => {

          this.payload = data.payload;
          this.post = this.payload.post;

          this.tags = this.post.tags;

          this.loading = false;
          this.linkWsp = `https://api.whatsapp.com/send?phone=${this.post.phone}&text=Hola,%20me%20gustar%C3%ADa%20consultar%20`;
          this.linkIg = `https://www.instagram.com/${this.post.instagram}/`;
        });
    });
  }
}
